.App {
  text-align: center;
  background-color: #191919;
  height: auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c342a;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes scrollBackground {
  0% {
    background-position: 100% 0; /* Position de départ */
  }

  50% {
    background-position: 50% 0; /* Position intermédiaire */
  }

  100% {
    background-position: 0 0; /* Position finale (identique à la position de départ pour boucler) */
  }
}

.header {
  position: relative;
  padding: 5px;
  padding-top: 9px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 80px;
  overflow: hidden;
  gap: 50px;
  z-index: 1;
  background-image: url('../img/header_for_animation.png');
  background-attachment: scroll, local;
  background-repeat: repeat;
  background-color: #02050c9c;
  background-blend-mode: overlay; /* Mélange le fond avec la couleur */
  animation: scrollBackground 15s linear infinite; 
}

.header::before {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
}

.logo {
  width: 10%;
  cursor: pointer;
  padding-left: 10px;
  padding-right: 10px;
  
}

.logo img {
  width: 90%;
  height: auto;
}

.menu {
  margin-left: 35px;
  width: 60%;
  height: 100%;
  position: relative;
}

.menu ul{
    width: 100%;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: aliceblue;
    font-size: 15px;
    display: flex;
    flex-direction: row;
    gap: 60px;
    overflow-x: hidden;
    align-content: flex-start;
    justify-content: flex-start;
    margin-top: -7px;
    @media screen and (max-width: 1410px) {
      font-size: 13px;
    }
    

  }



  .menu ul li {
    position: relative;
    cursor: pointer;
    border-top: 3.5px solid  transparent;
    padding-top: 35px;
    transition: padding-top 0.2s ease;
    z-index: 5;
  }

  .menu ul li:hover {
    color: yellow;
    padding-top: 37px;
  }

  .hideOnSmallScreen {
    @media screen and (max-width: 1310px) {
    display: none;
  }
}



.redHightlight {
  position: absolute;
  width: 100%;
  height: 4.5px;
  background-color: red;
  top: 0px;
  z-index: 9;
}

.yellowHightlight {
  position: absolute;
  width: 100%;
  height: 5px;
  background-color: yellow;
  top: -5px;
  z-index: 10;
  transition: top 0.2s ease;
}

.searchBar {
  width: 20%;
  height: auto;
}

.searchBar input[type=text]
{
  font-size: 16px;
  border: 0px;
  height: 30px;
  width: 80%;
  background-color: #19191936;
  border-bottom: 0.5px solid rgba(255, 255, 255, 0.438);
}


.body {
  display: flex;
  padding: 10px;
  width: 99%;
  justify-content: center;
  height: auto;
  gap: 1%;
  margin-bottom: 100px;
}

.leftContainer {
  width: 75%;
  height: auto;

@media screen and (max-width: 1500px) {
width : 68%;
}

@media screen and (max-width: 767px) {
  width: 99%;
}


}

.rightContainer {
  width: 23.9%;
  height: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
 
@media screen and (max-width: 1500px) {
  width : 30%;
  }

  @media screen and (max-width: 767px) {
    display: none;
  }

}

.rightContainer img {
  max-width: 100%;
  height: auto;
}


